import leftLogo from "../../assets/left-login-screen.svg";
import rightLogo from "../../assets/right-logo.svg";
import buildo from "../../assets/buildo-login.svg";
import TextField from "@mui/material/TextField";
import { VisibilityOff, Visibility } from "@mui/icons-material";
import {
  FormControl,
  InputLabel,
  OutlinedInput,
  InputAdornment,
  IconButton,
  Button,
} from "@mui/material";
import { useEffect, useState } from "react";
import { getIsUserLoggedIn, loginUser } from "../../services/appoloService";
import { Navigate, useNavigate } from "react-router-dom";

const LoginScreen = () => {
  const [showPassword, setShowPassword] = useState(false);
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [successLogin, setSuccessLogin] = useState(false);

  const handleClickShowPassword = () => {
    setShowPassword(!showPassword);
  };

  const handleMouseDownPassword = (
    event: React.MouseEvent<HTMLButtonElement>
  ) => {
    event.preventDefault();
  };

  const navigate = useNavigate();
  useEffect(() => {
    if (getIsUserLoggedIn()) {
      navigate("/");
    }
  }, [navigate]);

  return (
    <div
      style={{
        position: "fixed",
        height: "100vw",
        width: "100vw",
        left: 0,
        top: 0,
        backgroundColor: "white",
        zIndex: 10000,
        display: "flex",
        justifyContent: "space-between",
      }}
    >
      {successLogin && <Navigate to="/constructions" />}
      <img alt="logo" style={{ height: "100vh" }} src={leftLogo} />
      <div style={{ display: "flex", flexDirection: "column" }}>
        <img style={{ paddingTop: "30%" }} alt="buildo" src={buildo} />
        <span
          style={{
            fontSize: "48px",
            fontWeight: 400,
            color: "red",
            paddingTop: "20px",
          }}
        >
          web.
        </span>
        <span
          style={{
            fontSize: "20px",
            fontWeight: 400,
            color: "#3C3C3C",
            paddingTop: "60px",
          }}
        >
          Zadajte svoje prihlasovavcie daje.
        </span>
        <div
          style={{
            paddingTop: "30px",
            display: "flex",
            flexDirection: "column",
            justifyContent: "space-between",
            height: "220px",
          }}
        >
          <TextField
            value={email}
            onChange={(event) => setEmail(event.target.value)}
            label="Email"
            variant="outlined"
          />
          <FormControl variant="outlined">
            <InputLabel htmlFor="outlined-adornment-password">
              Password
            </InputLabel>
            <OutlinedInput
              value={password}
              onChange={(event) => setPassword(event.target.value)}
              id="outlined-adornment-password"
              type={showPassword ? "text" : "password"}
              endAdornment={
                <InputAdornment position="end">
                  <IconButton
                    aria-label="toggle password visibility"
                    onClick={handleClickShowPassword}
                    onMouseDown={handleMouseDownPassword}
                    edge="end"
                  >
                    {showPassword ? <VisibilityOff /> : <Visibility />}
                  </IconButton>
                </InputAdornment>
              }
              label="Password"
            />
          </FormControl>
          <Button
            variant="contained"
            onClick={async () => {
              const user = await loginUser(email, password);
              if (user) {
                setSuccessLogin(true);
              }
            }}
          >
            Prihlsi
          </Button>
        </div>
      </div>
      <img alt="right-logo" style={{ height: "50vh" }} src={rightLogo} />
    </div>
  );
};

export default LoginScreen;
