import { useQuery } from "@apollo/client";
import {
  Backdrop,
  FormControl,
  InputLabel,
  List,
  MenuItem,
  Select,
} from "@mui/material";
import { useMemo, useState } from "react";
import { ALL_CONSTRUCTIONS } from "../../queries/queries";
import { ConstructionListItem } from "../constructionListItem/constructionListItem";
import "./constructionsView.scss";

type State = "active" | "inactive" | "all";

const ConstructionView = () => {
  const { loading, data } = useQuery(ALL_CONSTRUCTIONS);
  const [selectedState, setSelectedState] = useState<State>("all");

  const renderedConstructions = useMemo(
    () =>
      data?.constructions
        .filter(
          (construction) =>
            (selectedState === "active" && construction?.isActive) ||
            (selectedState === "inactive" && !construction?.isActive) ||
            selectedState === "all"
        )
        .map((construction) => {
          return (
            <ConstructionListItem
              name={construction?.name || ""}
              investorName={construction?.investor?.name || ""}
              address={construction?.locality?.address || ""}
              isActive={!!construction?.isActive}
            />
          );
        }),
    [data, selectedState]
  );

  return (
    <div className="construction-view-container">
      <div className="construction-view-toolbar">
        Stavby
        <FormControl>
          <InputLabel id="demo-simple-select-label">Stav</InputLabel>
          <Select
            className="construction-view-filter"
            labelId="demo-simple-select-label"
            id="demo-simple-select"
            label="Stav"
            onChange={(e) => setSelectedState(e.target.value as State)}
            defaultValue={"all"}
          >
            <MenuItem value={"active"}>Prebiehajce</MenuItem>
            <MenuItem value={"inactive"}>Ukonen</MenuItem>
            <MenuItem value={"all"}>Vetky</MenuItem>
          </Select>
        </FormControl>
      </div>
      <div
        style={{ overflow: loading ? "hidden" : "scroll" }}
        className="construction-view-list-items-wrapper"
      >
        <List>{renderedConstructions}</List>
        <Backdrop className="construction-view-backdrop" open={loading}>
          <span className="loader"></span>
        </Backdrop>
      </div>
    </div>
  );
};

export default ConstructionView;
