import HouseIcon from "@mui/icons-material/House";
import PeopleIcon from "@mui/icons-material/People";
import BuildIcon from "@mui/icons-material/Build";
import BusinessIcon from "@mui/icons-material/Business";
import SettingsIcon from "@mui/icons-material/Settings";
import LogoutIcon from "@mui/icons-material/Logout";
import DrawerMenuItem from "../draverMenuItem/drawerMenuItem";
import DrawerMenu from "../drawerMenu/drawerMenu";
import { useEffect, useState } from "react";
import {
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
} from "@mui/material";
import { getIsUserLoggedIn, logoutUser } from "../../services/appoloService";
import { useNavigate } from "react-router-dom";

const drawerMenuItems: [string, JSX.Element, string][] = [
  ["Stavby", <HouseIcon />, "constructions"],
  ["Pracovnci", <PeopleIcon />, "workers"],
  ["Stroje", <BuildIcon />, "machines"],
  ["daje o firme", <BusinessIcon />, "organization"],
  ["Nastavenia", <SettingsIcon />, "settings"],
];

const LeftMenu = () => {
  const [selectedMenuItem, setSelectedMenuItem] = useState(0);

  const navigate = useNavigate();
  useEffect(() => {
    if (!getIsUserLoggedIn()) {
      navigate("/login");
    }
  }, [navigate]);

  const renderedItems = drawerMenuItems.map((item, index) => (
    <DrawerMenuItem
      onClick={() => setSelectedMenuItem(index)}
      selected={index === selectedMenuItem}
      icon={item[1]}
      text={item[0]}
      href={item[2]}
    />
  ));

  renderedItems.push(
    <ListItem disablePadding>
      <ListItemButton
        onClick={async () => {
          await logoutUser();
          navigate("/login");
        }}
      >
        <ListItemIcon>
          <LogoutIcon />
        </ListItemIcon>
        <ListItemText primary="Odhlsi" />
      </ListItemButton>
    </ListItem>
  );

  return <DrawerMenu>{renderedItems}</DrawerMenu>;
};

export default LeftMenu;
