import { ApolloClient, HttpLink, InMemoryCache } from "@apollo/client";
import * as Realm from "realm-web";
const APP_ID = "buildo-3-cmltm";
const app = new Realm.App(APP_ID);

const GRAPHQL_URL = `https://eu-central-1.aws.realm.mongodb.com/api/client/v2.0/app/${APP_ID}/graphql`;

async function getValidAccessToken() {
  if (app.currentUser) {
    await app.currentUser.refreshAccessToken();
    return app.currentUser?.accessToken;
  }
}

export const loginUser = async (email: string, password: string) => {
  try {
    return await app.logIn(Realm.Credentials.emailPassword(email, password));
  } catch {
    return undefined;
  }
};

export const getIsUserLoggedIn = () => {
  return !!app.currentUser?.isLoggedIn;
};

export const logoutUser = async () => {
  await app.currentUser?.logOut();
};

export const getApolloClient = () =>
  new ApolloClient({
    link: new HttpLink({
      uri: GRAPHQL_URL,
      fetch: async (uri, options) => {
        const accessToken = await getValidAccessToken();
        if (options?.headers)
          options.headers = [["Authorization", `Bearer ${accessToken}`]];
        return fetch(uri, options);
      },
    }),
    cache: new InMemoryCache(),
  });
